.border-b {
  border-bottom: 1px solid #ebebeb;
}
.clearfix {
  clear: both;
}
.basic-info {
  padding-bottom: 15px;
}
.basic-info .left,
.basic-info .right {
  float: left;
}
.basic-info .left {
  width: 80px;
}
.basic-info .right {
  width: calc(100% - 80px);
  padding-top: 2px;
}
.basic-info .right .user-name {
  font-size: 20px;
  font-weight: 500;
}
.basic-info .right .user-desc {
  padding-top: 5px;
  font-size: 14px;
}
.user-item {
  padding: 20px 0;
  font-size: 16px;
  position: relative;
}
.user-item .ant-col:nth-child(2n) {
  padding-right: 20px;
  color: #888;
  cursor: pointer;
}
.user-item:before {
  content: "";
  width: 10px;
  height: 10px;
  border-top: 2px solid #888;
  border-right: 2px solid #888;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
  position: absolute;
  right: 2px;
  top: 50%;
  margin-top: -5px;
}
.btn-operation {
  margin-top: 20px;
}
.btn-operation button {
  height: 40px;
  line-height: 40px;
}
.btn-operation button:first-child {
  margin-bottom: 10px;
}
