  background: #fff url(/static/media/welcome-bg.5825f033.svg) no-repeat;
  background-position: center 110px;
  background-size: 100%;
}
.container .layout-right .empty-page img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
