  overflow-y: auto;
}
.container .contacts-list .user-search {
  padding: 14px 12px 16px;
}
.container .contacts-list .user-search .input-search .ant-input {
  background-color: #f5f5f5;
  display: block;
  width: 100%;
  height: 32px;
  border: none;
  outline: none;
  border-radius: 2px;
}
.container .contacts-list .user-search .btn-plus {
  border: none;
  color: #666;
  background: #f5f5f5;
  border-radius: 2px;
}
.container .contacts-list .conversation-list {
  max-height: calc(100vh - 62px);
  overflow: auto;
}
.container .contacts-list .conversation-list .ant-list-item {
  position: relative;
  cursor: pointer;
  padding: 12px 12px 10px;
}
.container .contacts-list .conversation-list .ant-list-item.active,
.container .contacts-list .conversation-list .ant-list-item:hover {
