  right: 12px;
  top: 10px;
}
.container .contacts-list .conversation-list .ant-list-item-meta-description {
  max-height: 25px;
  max-width: 170px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.container .mail-users {
  height: 100%;
}
.container .mail-users .mail-list {
  min-height: calc(100% - 62px);
  max-height: calc(100% - 62px);
  overflow: auto;
}
.container .mail-users .mail-list .m-list-item {
  cursor: pointer;
}
.container .mail-users .mail-list .m-list-item .m-list-title {
