  height: 40px;
  margin-top: 4px;
}
.container .mail-users .mail-list .m-list-item .ant-list-item-meta-title {
  line-height: 44px;
  margin-bottom: 0;
  max-height: 40px;
  max-width: 160px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.container .message-container {
  height: 100%;
  overflow: hidden;
}
.container .message-container .message-header {
  background: #fff;
  padding: 0 20px;
  height: 62px;
  overflow: hidden;
  z-index: 100;
}
.container .message-container .message-header .ant-avatar {
  margin-top: -5px;
}
.container .message-container .message-header .icon-more {
  color: #000;
  font-size: 30px;
  vertical-align: middle;
}
.container .message-container .message-body {
  min-height: calc(100% - 210px);
  max-height: calc(100% - 210px);
  overflow: auto;
  background: #f5f5f5;
  padding: 20px;
}
.container .message-container .message-body .message-left,
.container .message-container .message-body .message-right {
  margin-bottom: 15px;
}
.container .message-container .message-body .message-left .message span,
.container .message-container .message-body .message-right .message span {
  border: 1px solid #ebebeb;
  word-break: break-all;
  padding: 8px 10px;
  border-radius: 5px;
  min-width: 50px;
  max-width: 60%;
}
.container .message-container .message-body .message-left .message span:after,
.container .message-container .message-body .message-right .message span:after {
  content: "";
  width: 0;
  height: 0;
  border: 6px solid transparent;
  position: absolute;
  top: 14px;
}
.container .message-container .message-body .message-left .message span {
  background-color: #fff;
  float: left;
}
.container .message-container .message-body .message-left .message span:after {
  border-right-color: #fff;
  left: -11px;
}
.container .message-container .message-body .message-right .message span {
  background-color: #a2e756;
  float: right;
}
.container .message-container .message-body .message-right .message span:after {
  border-left-color: #a2e756;
  right: -11px;
}
.container .message-container .message-body .message-right .pull-left,
.container .message-container .message-body .message-right .pull-right {
  position: relative;
}
.container .message-container .message-body .message-right .pull-left {
  width: calc(100% - 44px);
  float: left;
}
.container .message-container .message-body .message-right .pull-right {
  width: 44px;
  float: right;
  text-align: right;
}
.container .message-container .message-footer {
  background: #fff;
  text-align: left;
  padding: 10px 20px 16px 18px;
