  min-height: 146px;
  max-height: 146px;
}
.container .message-container .message-footer .chat-tools {
  height: 18px;
  line-height: 18px;
}
.container .message-container .message-footer .chat-textarea {
  width: 100%;
  min-height: 70px;
  padding-right: 24px;
  white-space: pre-line;
  border: none;
  resize: none;
  outline: none;
  padding-left: 5px;
}
.container .setting-sider {
